
#Landing{
    width: 100vw;
    height: 100vh;
    padding: 0;
    display: grid;
    place-content: center;
}
.landing-content-wrapper{
    width: fit-content;
}
.landing-titles{
    margin: 0 auto;
}
.landing-title{
    font-weight: 350;
    font-size: clamp(48px, 8vw, 100px);
    color: var(--purple);
    width: fit-content;
}
.landing-title span{
    color: var(--red);
}
.landing-underline{
    width: clamp(325px, 70vw, 1100px);
}

.landing-subtitle-wrapper{
    width: clamp(280px, 50vw, 610px);
    margin: auto;
    display: flex;
    justify-content: space-around;

}
.landing-subtitle-wrapper h3 {
    font-size: clamp(24px, 3.5vw, 48px);
}


/* Landing Navigation */
.hidden {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 1s, opacity 1s linear;
  }
.landing-nav-wrapper{
    position: absolute;
    bottom: 3rem;
    width: 100%;
    transition: 1s;
}
.landing-nav-bar{
    display: flex;
    justify-content: space-between;
    width: clamp(275px, 45vw, 500px);
    margin: auto;
    padding: 0;
}

.landing-link{
    font-size: clamp(14px, 2vw, 20px);
}